section {
  margin-top: 100px;
  .sm(margin-top, 0px);
  .section_introduce {
    .section_introduce_img {
      width: 100%;
      height: auto;
      img {
        width: 100%;
        height: 600px;
        max-width: 100%;
      }
    }
    h2 {
      text-transform: uppercase;
      margin-left: 60px;
      font-size: 30px;
      font-weight: 900;
      color: #333333;
      padding-bottom: 40px;
      .sm(font-size, 20px);
      span {
        color: #ef5050;
      }
      .lg-block({
                    margin-top: 20px;
                    text-align: center;
                    margin-left: 0;
                });
      .md(padding-bottom, 20px);
    }
    p {
      color: #646464;
      font-size: 15px;
      margin-left: 60px;
      line-height: 24px;
      font-weight: 700;
      margin-bottom: 64px;
      .lg-block({
                    text-align: center;
                    margin-left: 30px;
                });
      .md(margin-bottom, 20px);
      .sm-block({
                    text-align: justify;
                    margin-left: 0px;
                });
    }

    .introduce_img {
      width: 40%;
      height: auto;
      margin-left: 60px;
      margin-right: 25px;
      .lg-block({
                    margin-left: 30px;
                    margin-right: 15px;
            } );
      .sm(margin-left, 0px);
      img {
        border: 2px solid grey;
        padding: 10px;
        &:hover {
          box-shadow: 2px 2px 2px black;
        }
      }
    }
    .introduce_text {
      h3 {
        text-transform: capitalize;
        color: #333333;
        font-weight: 700;
        font-size: 18px;
        margin-bottom: 24px;
      }
      p {
        margin-left: 00px;
        margin-bottom: 30px;
        font-size: 14px;
        font-weight: 400;
        color: #8b8b8b;
        .lg-block({text-align: left;
                     margin-bottom: 10px;   }) !important;
      }

      .intorduce_read {
        margin-bottom: 60px;
        font-size: 13px;
        font-weight: 700;
        text-transform: capitalize;
        color: #8b8b8b;
        display: block;
        &:hover {
          color: #ef5050;
          text-shadow: 2px 2px 2px black;
        }
        .lg(margin-bottom, 20px);
      }
    }
  }

  .section_service {
    margin-top: 140px;
    .md(margin-top, 50px);
    h2 {
      font-size: 30px;
      font-weight: 900;
      color: #333333;
      margin-bottom: 50px;
      text-transform: uppercase;
      text-align: center;
      .sm-block({
        margin-bottom: 15px;
        font-size: 19px;
      });
      .md-block({
        margin-bottom: 30px;
        font-size: 25px;
      });
    }
    p {
      font-weight: 700;
      font-size: 15px;
      color: #333333;
      text-align: center;
      margin-bottom: 65px;
      line-height: 28px;
      .md(margin-bottom, 30px );
      .sm-block({
        margin-bottom: 15px;
        text-align: justify;
      });
    }
    .service_box {
      display: flex;
      flex-wrap: wrap;
      .md(justify-content, center);
      .service-all {
        display: block;
        margin: auto;
        text-transform: uppercase;
        margin-top: 70px;
        background: transparent;
        border: 2px solid #ef5050;
        padding: 15px 30px;
        font-size: 14px;
        font-weight: 700;
        margin-bottom: 100px;
        .md(margin-bottom, 50px);
        .sm(margin-bottom, 15px);
      }
    }

    .service_info {
      position: relative;
      .lg-block({
        margin: 10px 0px;
      });
      .service_img {
        position: relative;
        width: 100%;
        .md(text-align, center);
        .bottom_img {
          width: 100%;
          position: absolute;
          bottom: -5px;
          right: 0px;
          .lg(transform, translate(-12px, 0px ));
          .md(transform, translate(0px, 0px ));
          background: url(../img/05.png) center center;
          background-repeat: no-repeat;
          p {
            font-weight: 700;
            font-size: 18px;
            color: white;
            padding: 26px 48px;
            margin: 0px;
            text-transform: capitalize;
            text-align: center;
          }
        }
      }
      .service_text {
        position: absolute;
        top: 0;
        background-color: #ed5656;
        padding: 80px 31px 70px;
        display: none;
        .service_theme {
          font-size: 18px;
          font-weight: 700;
          color: white;
          text-transform: capitalize;
          text-align: center;
          margin-bottom: 20px;
        }
        p {
          font-size: 14px;
          font-weight: 400;
          margin-bottom: 0px;
          color: white;
        }
        .read_more {
          display: none;
        }
        .service_btn {
          text-transform: uppercase;
          display: block;
          margin: auto;
          padding: 8px 12px;
          background: transparent;
          border: 2px solid white;
          color: white;
          font-size: 14px;
          font-weight: 700;
          margin-top: 30px;
        }
      }
    }
  }

  .section_gallery {
    background-color: #616161;
    padding: 130px;
    h2 {
      text-align: center;
      text-transform: uppercase;
      color: white;
      font-size: 30px;
      font-weight: 900;
      margin-bottom: 37px;
    }
    p {
      color: #b1b1b1;
      font-size: 15px;
      font-weight: 400;
      text-align: center;
      margin-bottom: 90px;
      line-height: 20px;
    }
    .owl-carousel {
      position: relative;
      .owl-nav {
        top: -50px;
        position: absolute;
        right: 46%;
        button {
          background: transparent;
          border: 2px solid white;
          margin: 0px 5px;
          width: 30px;
          height: 30px;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          &:hover {
            background: #ed5656;
            border: none;
          }
        }
        span {
          color: white;
          font-size: 15px;
        }
      }
    }
  }

  .section_team {
    padding-top: 100px;
    .sm(padding-top, 50px);
    h2 {
      text-transform: uppercase;
      font-weight: 900;
      font-size: 30px;
      color: black;
      margin-bottom: 30px;
      text-align: center;
      .lg(margin-bottom, 15px);
      .sm-block({
        margin-bottom: 15px;
        font-size: 19px;
      });
    }
    p {
      font-weight: 700;
      font-size: 15px;
      color: black;
      text-align: center;
      margin-bottom: 70px;
      margin-top: 0px;
      .sm-block({
        margin-bottom: 15px;
        text-align: justify
      });
    }
    p:last-of-type {
      font-size: 18px;
      color: black;
      font-weight: 400;
      text-align: center;
      font-style: italic;
      margin-bottom: 62px;
      margin-top: 56px;
      .sm-block({
        margin: 15px 0px;
         
      });
    }
    .imges_expert_team {
      width: 100%;
      height: auto;
      img {
        width: 100%;
        max-width: 100%;
        height: 100%;
      }
    }
    .team_surname {
      text-transform: uppercase;
      text-align: center;
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 0px;
      span {
        display: block;
        margin-top: 20px;
        font-size: 14px;
        font-weight: 400;
        color: #9d9d9d;
        text-align: center;
        text-transform: capitalize;
        .sm(margin-top, 10px );
      }
    }

    .expert_team {
      margin-top: 0px !important;
      margin-bottom: 80px;
      .lg(margin-bottom, 35px);
    }

    .expert_text {
      .lg-block({
        margin: 15px 0px;
      });
      h3 {
        margin-top: 30px;

        font-weight: 700;
        font-size: 18px;
        text-align: left;
        color: black;
        text-transform: capitalize;
        span {
          color: #b1b1b1;
          font-size: 13px;
          font-weight: 400;
          text-transform: capitalize;
          margin-top: 12px;
          padding-bottom: 20px;
          border-bottom: 2px solid #b1b1b1;
          display: block;
        }
      }
    }
    .expert_icons {
      margin-top: 20px;
      a {
        text-decoration: none;
        i {
          font-size: 18px;
          padding: 11px;
          color: white;
          margin-right: 11px;
          background-color: #ef5050;
        }
      }
    }
    .team_carousel {
      position: relative;
      .prev {
        position: absolute;
        bottom: 0;
        right: 40%;
      }
      .next {
        position: absolute;
        bottom: 0;
        right: 20%;
      }

      width: 400px;
      height: 100px;
      .team_carousel_img {
        width: 400px;
        position: absolute;
        top: -50px;
        overflow: hidden;
        height: 100px;
        border: 2px solid black;
        display: flex;
      }
      img {
        border-radius: 50%;
        transform: translateX(0px);
        margin: 0px 20px;
      }
    }
  }

  .section_form {
    background: url(../img/carousel_03.jpg) center center / cover;
    background-attachment: fixed;
    margin-top: 120px;
    .md(margin-top, 50px);
    background-repeat: no-repeat;
    h3 {
      font-size: 30px;
      color: #ef5050;
      text-transform: uppercase;
      margin-bottom: 30px;
      font-weight: 900;
      text-align: center;
      padding-top: 200px;
    }
    p {
      color: #ef5050;
      font-size: 20px;
      font-weight: 700;
      text-align: center;
    }
    .forms {
      background-color: #000000;
      padding: 45px;
      transform: translateY(120px);
      .md(padding, 20px);

      input {
        background-color: transparent;
        border: 2px solid #616161;
        color: #8b8b8b;
        margin: 0px 10px;
        padding: 10px 23px;
        .md-block({
          width: 100%;
          margin: 10px 0px;
          display: block;
        });
      }
      textarea {
        background: transparent;
        border: 2px solid #616161;
        color: #8b8b8b;
        margin: 10px 0px;
        width: 100%;
        padding: 10px 23px;
      }
      .send {
        padding: 16px 40px;
        color: white;
        background-color: #ef5050;
        border: none;
        display: block;
        margin: auto;
        text-transform: uppercase;
      }
    }
  }

  .section_news {
    margin-top: 240px;
    .md(margin-top, 180px);
    h2 {
      font-weight: 900;
      font-size: 30px;
      color: black;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 40px;
      .md(margin-top, 20px);
      .sm-block({
        margin-bottom: 15px;
        font-size: 19px;
      });
    }
    p {
      font-weight: 700;
      font-size: 15px;
      color: black;
      text-align: center;
      text-transform: capitalize;
      margin-bottom: 70px;
      display: block;
      .md(margin-bottom, 50px);
    }
    .news_img {
      width: 100%;
      height: auto;
      .md(text-align, center);
    }
    .news_text {
      display: flex;
      margin: 20px 0px;
      .data {
        font-weight: 700;
        font-size: 24px;
        text-align: center;
        padding: 18px 22px;
        color: white;
        margin-bottom: 0;
        background-color: #ef5050;
        span {
          display: block;
          font-size: 14px;
        }
      }
      .title {
        font-weight: 700;
        font-size: 20px;
        color: black;
        text-align: left;
        margin-left: 8px;
        margin-bottom: 16px;

        span {
          display: inline-block;
          font-size: 13px;
          font-weight: 400;
          color: #8b8b8b;
          text-align: center;
          .md(margin-left, 60px);
        }
      }
    }

    .news_text_bottom {
      p {
        text-align: left;
        color: #8b8b8b;
        font-size: 14px;
        font-weight: 400;
        margin-bottom: 20px;
        line-height: 20px;
      }
      a {
        text-decoration: none;
        color: black;
        font-size: 13px;
        font-weight: 700;
      }
      h3 {
        text-align: left;
        font-size: 18px;
        font-weight: 700;
        color: black;
        text-transform: capitalize;
        margin-bottom: 20px;
      }
      .car_cleaning {
        margin-bottom: 45px;
        margin-top: 25px;
        p {
          font-size: 14px;
          font-weight: 900;
          margin-bottom: 25px;
          color: black;
          i {
            color: #ef5050;
            margin-right: 15px;
          }
        }
      }
    }
    .tags {
      display: flex;
      justify-content: space-between;
      padding: 20px 0px;
      border-top: 2px solid #b1b1b1;
      border-bottom: 2px solid #b1b1b1;
      margin-bottom: 70px;
      margin-top: 20px;
      .tags_part {
        display: flex;
        align-items: center;
        p {
          margin-bottom: 0;
          font-weight: 700;
          text-transform: uppercase;
          font-size: 13px;
          color: black;
          margin-right: 5px;
        }
        a {
          color: #8b8b8b;
          text-decoration: none;
          margin: 0px 3px;
          &:hover {
            color: #ef5050;
          }
        }
      }
      .social_media {
        a {
          color: #8b8b8b;
          text-decoration: none;
          margin: 0px 3px;
          font-size: 13px;
          display: inline-block;

          &:hover {
            color: #ef5050;
          }
        }
      }
    }
    .comments {
      h3 {
        text-align: left;
        font-size: 24px;
        font-weight: 700;
        color: black;
        text-transform: capitalize;
        margin-bottom: 20px;
      }
      .comment_all:nth-child(odd) {
        margin-left: 100px;
      }
      .detail_comment {
        display: flex;
        align-items: flex-start;
        .comment_img {
          width: 30%;
        }

        p {
          display: flex;
          flex-direction: column;
          text-transform: uppercase;
          font-size: 14px;
          font-weight: 700;
          color: black;
          width: 100%;
          text-align: left;
          margin-bottom: 0;
          span {
            font-size: 16px;
            color: #8b8b8b;
            text-transform: capitalize;
            font-weight: 400;
            margin-top: 10px;
          }
        }
        .comment_reply {
          align-self: flex-start;
          text-align: right;
          &:hover {
            color: #ed5656;
          }
        }
      }

      .comment_points {
        text-align: left;
        margin-top: 25px;
        font-size: 13px;
        font-weight: 400;
        color: #7c7c7c;
        line-height: 30px;
      }
    }
    .leave_comment {
      h3 {
        text-align: left;
        font-size: 24px;
        font-weight: 700;
        color: black;
        text-transform: capitalize;
        margin-bottom: 20px;
      }
      .form_comment {
        input {
          padding: 20px 0px 20px 25px;
          text-transform: uppercase;
          font-weight: 400;
          font-size: 13px;
          color: #acacac;
          border: none;
          background-color: #f3f3f3;
          margin-bottom: 20px;
        }
        input[placeholder="subject"] {
          width: 100%;
        }
        input[type="email"] {
          width: 48%;
          margin-left: 10px;
        }
        input[placeholder="name"] {
          width: 48%;
          margin-right: 10px;
        }
        textarea {
          width: 100%;
          padding: 20px 0px 20px 25px;
          text-transform: uppercase;
          font-weight: 400;
          font-size: 13px;
          color: #acacac;
          border: none;
          background-color: #f3f3f3;
          margin-bottom: 20px;
        }
      }
      .b_comment {
        color: white;
      }
    }

    .news_tabs {
      .lg-block({
        margin-top: 20px ;
      });
      .news_tabs_title {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
        h3 {
          font-weight: 700;
          font-size: 18px;
          color: black;
          text-transform: capitalize;
        }
      }

      .tabs_text {
        display: none;
        p {
          font-weight: 400;
          font-size: 14px;
          color: #8b8b8b;
          text-align: left;
        }
      }
    }
    .news_search {
      margin-bottom: 65px;
      input {
        padding: 20px 0px 20px 23px;
        background: transparent;
        border: 2px solid #8b8b8b;
        position: relative;
        width: 100%;
        text-transform: capitalize;
      }
      .search {
        background: none;
        border: none;
        position: absolute;
        top: 20px;
        right: 30px;
        font-size: 18px;
        color: #8b8b8b;
      }
    }
    .news_posts {
      h3 {
        font-size: 24px;
        font-weight: 700;
        color: black;
        text-transform: capitalize;
        margin-bottom: 45px;
      }
      .latest_post {
        display: flex;
        justify-content: space-between;
        .post_text {
          margin-left: 30px;
          p {
            font-size: 14px;
            font-weight: 700;
            text-align: left;
            line-height: 18px;
            margin-bottom: 20px;
          }
          p:last-of-type {
            color: #ef5050;
            display: flex;
            justify-content: space-between;
            font-weight: 400;
            font-size: 12px;
            text-transform: uppercase;
            span {
              color: #bcbcbc;
              text-transform: capitalize;
            }
          }
        }
      }
    }
    .catagories {
      margin-top: 70px;
      h3 {
        font-weight: 700;
        font-size: 24px;
        color: black;
        text-transform: capitalize;
        margin-bottom: 10px;
      }
      ul {
        li {
          padding: 20px 0px;
          a {
            text-decoration: none;
            text-transform: capitalize;
            font-size: 14px;
            font-weight: 400;
            color: #8b8b8b;
          }
        }
      }
    }

    .archives {
      margin-top: 70px;
      h3 {
        font-weight: 700;
        font-size: 24px;
        color: black;
        text-transform: capitalize;
        margin-bottom: 10px;
      }
      ul {
        li {
          padding: 20px 0px;
          a {
            text-decoration: none;
            text-transform: capitalize;
            font-size: 14px;
            font-weight: 400;
            color: #8b8b8b;
          }
        }
      }
    }
    .keyword {
      margin-top: 70px;
      h3 {
        font-weight: 700;
        font-size: 24px;
        color: black;
        text-transform: capitalize;
        margin-bottom: 10px;
      }
      .keyword_btn {
        padding: 8px 20px;
        border: none;
        background: #4e4e4e;
        color: white;
        text-transform: capitalize;
        margin: 25px 6px 0px;
        font-size: 12px;
        font-weight: 400;
        transition: 5ms;
        &:hover {
          background: #ef5050;
        }
      }
    }
  }

