header {
  background-color: #1d1d1d;
  width: 100%;
  height: auto;
  .sm(padding-top, 20px);
  .header_top {
    padding-top: 35px;
    position: relative;

    .logo_box {
      display: flex;
      .sm(justify-content, center);
      .logo_img {
        max-width: 100%;
        height: auto;
        margin-right: 20px;
        .lg(margin-right, 5px );
      }
      .logo {
        font-weight: 700;
        color: #fff;
        font-size: 30px;
        text-transform: uppercase;
        text-decoration: none;

        span:first-child {
          font-size: 30px;
          color: #ef5050;
        }
        .logo_bottom {
          font-size: 12px;
          color: #8a8a8a;
          display: block;
          text-transform: none;
          margin-bottom: 40px;
        }
      }
    }

    .header_contact {
      color: #fff;
      font-size: 14px;
      font-weight: 400;
      text-transform: uppercase;
      margin-bottom: 10px;
    }

    .header_info {
      color: #727272;
      font-size: 13px;
      font-weight: 400;
      display: block;
      .md(padding-bottom, 20px);
    }
    .social_icon {
      align-self: center;
      .sm-block({
        display: flex;
      justify-content: center;
      margin-bottom: 20px;
      });
      a {
        text-decoration: none;
        font-size: 20px;
        margin-right: 9px;
        i {
          color: white;
          border: 2px solid white;
          border-radius: 50%;
          padding: 8px;
          &:hover {
            background-color: #ef5050;
            border: none;
            border-radius: 50%;
            padding: 8px;
          }
        }
      }
    }

    .phone_box {
      display: flex;

      .icons_phone {
        margin-right: 20px;
        .lg-block({
                margin-right: 5px;
                align-self: flex-end;
                
            });

        i {
          font-size: 35px;
          color: white;
          transition: 5ms;
          .lg-block({
                    font-size: 15px; 
                    
                });
          &:hover {
            color: #ef5050;
            transform: rotate(-30deg);
            text-shadow: 2px 2px 20px #000000;
          }
        }
      }
      .phones {
        text-decoration: none;

        .header_contact {
          font-size: 14px;
          text-transform: capitalize;
        }
        .logo_bottom {
          color: white;
          font-size: 18px;
          .lg( font-size, 13px );
        }
      }
    }

    nav {
      .sm(background-color, #ef5050);
      .navbar {
        position: relative;
        .nav {
          display: flex;
          background-color: #ef5050;
          padding: 30px 42px;
          justify-content: space-between;
          .md-block({display: none;
                   box-shadow: 2px 2px 2px #ef5050;
                   border-radius: 2px;
    });
          .sm(padding, 10px);

          li {
            padding: 0px 5px;
            .md-block({padding: 10px 0px;
                        border-bottom: 2px solid #373737 ;
                        border-radius: 12px;
                        
                    });
            a {
              text-decoration: none;
              color: #fff;
              font-size: 14px;
              font-weight: 400;
              text-transform: uppercase;
              .lg(font-size , 10px );
              .md( font-size, 16px );
            }
          }
        }
        #bars {
          color: #000;
          font-size: 30px;
          margin-right: 20px;
          top: -9px;
          position: absolute;
          right: 50%;
          display: none;
          .md(display, block);
          border: none;
          background: transparent;
          text-shadow: 2px 2px 2px black;
          z-index: 11;
          color: wheat;
        }
      }
    }
  }
  .owl-carousel {
    position: relative;

    .carosel_img {
      width: 100%;
      height: 600px;
      position: relative;

      img {
        max-width: 100%;
        height: 600px;
      }
      .carousel_text {
        position: absolute;
        top: 200px;
        width: 100%;
        
        .text_black {
          background: url(../img/004.png) center center;
          background-repeat: no-repeat;
          h1 {
            font-size: 45px;
            color: white;
            text-transform: uppercase;
            text-align: center;
            font-weight: 900;
          }
        }
        .text_white {
          background: url(../img/003.png) center center;
          background-repeat: no-repeat;
          margin: auto;
          h2 {
            font-size: 35px;
            font-weight: 900;
            text-transform: uppercase;
            text-align: center;
            color: #373737;
          }
        }
        p {
          text-align: center;
          color: white;
          font-size: 20px;
          font-weight: 400;
          margin-top: 24px;
          margin-bottom: 45px;
        }
        .btn_contact {
          text-transform: uppercase;
          font-size: 13px;
          font-weight: 700;
          background: transparent;
          display: block;
          margin: auto;
          padding: 14px 25px;
          border: 2px solid #fff;
          color: white;
          &:hover {
            box-shadow: 2px 2px 2px black;
            color: #ef5050;
            border: 2px solid #ef5050;
          }
        }
      }
    }
    .owl-dots {
      right: 47%;
      bottom: 10%;
      position: absolute;

      .owl-dot {
        background-color: white;
        color: white;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        margin: 0px 5px;
        right: 200px;
      }
    }
 
  }
  .contact_modal {
    transform: translate(600px, -350px);
    background-color: rgba(0, 0, 0, 0.5);
    width: 300px;
    height: 300px;
    border: 2px solid #ef5050;
    border-radius: 5px;
    padding-top: 10px;
    position: fixed;
    top: 70%;
    z-index: 1111;
    box-shadow: 3px 1px 14px 3px rgba(191,15,15,0.69);
    display: none;
    p {
      font-size: 15px;
      font-weight: 400;
      text-align: center;
      border-top: 2px solid gray;
      border-bottom: 2px solid gray;
      margin-bottom: 20px;
      text-transform: uppercase;
      color: wheat;
      padding: 20px 0px;
    }
    a {
      color: white;
      text-decoration: none;
      display: block;
      text-align: center;
      font-size: 18px;
      i {
        font-size: 30px;
        margin-right: 5px;
      }
    }
    .contact_close {
      position: absolute;
      top: -7px;
      right: -10px;
      font-size: 20px;
      background: none;
      color: wheat;
      border: none;
    }
  }
  .modal_bg_opacity{
    position: fixed;
    width: 100%;
    height: 100%;
    display: none;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0, .8);
    z-index: 111;
  }
}
